top-bar {
  background-color: transparent;

  .col-xs:nth-of-type(3) {
    border-left: 0;
  }

  .search-form {
    .search-group {
      border-bottom: 1px solid #ffffff;
      input {
        color: #ffffff;
      }
      button {
        iron-icon {
          color: #ffffff;
        }
      }
    }
  }

  .member {
    &__action {
      paper-button {
        iron-icon {
          color: #ffffff;
        }
      }
    }
    &__toggle-nav {
      color: #ffffff;
    }
  }
}