@import "../../../../public/bower_components/sassstd/src/sassstd";
@import "../../../../public/bower_components/scss-zhilizhili-mei/stylesheets/mei";

@import "../../framework/sass/utils/get-random-color";

@import "../../framework/sass/utils/component";
@import "../../framework/sass/utils/bem";
@import "audioplayer";

@import "topbar-transparent-theme";

.nav-item {
  width: 960px;
  height: 450px;
  position: absolute;
  font-size: 60px;
  color: #ffffff;
  text-align: center;
  line-height: 450px;
}

.nav-item.active {
  left: 0 !important;
  top: 0 !important;
  z-index: 1000 !important;
  width: 100vw !important;
  height: 100vh !important;
  opacity: 1 !important;
}

.nav-item.hide {
  top: 1024px !important;
  opacity: 0 !important;
}

@for $i from 1 through 5 {
  .nav-item:nth-of-type(#{$i}) {
    left: #{60 * ($i - 1) + 120}px;
    top: #{-60 * ($i - 1) + 260}px;
    z-index: 5 - $i;
    transition: all 1s ease;
    opacity: (5 - $i * 1.2 + 1) * 0.2;
  }
}

html, body {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

view-controller {
  display: block;
  width: 100%;
  height: calc(100% - 95px);
  > .view-controller {
    width: 100%;
    height: 100%;
  }
}

main {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

$my-blue: #63949d;

@include component("silder-nav") {
  @include block(".silder-nav") {
    @include modifiy("default") {
      list-style-type: none;
      width: 30px;
      padding-left: 0;
      border: 1px solid $my-blue;
      text-align: center;
      cursor: pointer;
      margin: 0;

      position: relative;
      left: 40px;
      top: 150px;
      @include element("item") {
        margin-top: 15px;
        margin-bottom: 15px;

        @include register-hook("--slider-nav--default slider-nav__item");

        &.iron-selected {
          background-color: $my-blue;
          color: #ffffff;
        }
      }
    }
  }
}

@include component("gallery-figure") {
  @include block(".gallery-figure") {
     @include modifiy("default") {
       width: 100%;
       height: 100%;
       margin:  0;
       overflow: hidden;

       @include element("img") {
         width: 100%;
         height: 100%;
       }
     }
  }
}

@include component("member") {
  @include block(".member") {
    @include modifiy("default") {
      position: relative;
      @include element("logo") {
        background-color:#ffffff;
      }
    }
  }
}

.body-background {
  background-color: #00b3ee;
  background-image: url("/assets/pc/img/index-bg-1x.jpg");
  background-size: 150%;
}

@include component("vis-audio-player") {
  @include block(".vis-audio-player") {
    position: absolute;
    left: 0;
    top: 230px;
    z-index: 9999;
    width: 100%;
    @include modifiy("default") {
      @include element("list") {
        color: #ffffff;
        @include register-hook("vis-audio-player vis-audio-player__list");
      }
    }
  }
}

html {
  @include use-hook("--slider-nav--default slider-nav__item", $scope: #{&}) {
    padding-top: 0;
    padding-bottom: 0;

    .btn {
      width: 100%;
      height: 100%;
      color: #ffffff;
      padding-top: 10px;
      padding-bottom: 10px;
    }
  }

  /// name: 可视化audio player list
  @include use-hook("vis-audio-player vis-audio-player__list") {
    a {
      color: #ffffff;
    }
  }
}

#videorange + .rangeSlider {
  .rangeSlider__fill {
    z-index: 2;
  }
}
#videoprogress + .rangeSlider {
  top: -3px;
  .rangeSlider__handle {
    display: none;
  }
  .rangeSlider__fill {
    z-index: 1;
    background: lighten(#3367D6, 27%);
  }
}



